<template>
  <nav>
    <v-app-bar flat app  color="#ffffff" light outlined >
      <img src="../assets/img/menu-black.png"  alt="close" class="iconClose mr-6" @click.stop="drawer = !drawer">
      <v-toolbar-title class="text-uppercase navTittle">
        <div v-if="showMainTitle">
          Методики самозасыпания для детей от 4 мес. до 2 лет
        </div>
        <div v-if="!showMainTitle">
          {{numberOfMethod[IS_NUMBER_METHOD]}}
        </div>

      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-navigation-drawer height="100%" color="#4464a3" flat v-model="drawer" dark app  >
      <v-layout column align-center>
      </v-layout>
      <v-list flat>
          <v-list-item
          class="mt-2"
          v-for="link in links"
          :key="link.id"
          active-class="border"
          @click="doLinks(link.id)"
        >
          <v-list-item-content class="itemTitle">
            <v-list-item-title >
              <router-link
                :to="link.link">
                <p class="links">
                  {{ link.text }}
                </p>
              </router-link>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>
<script>
// import vLanguageSwitcher from "../v-language-switcher/v-language-switcher"
import { mapActions, mapGetters } from 'vuex'
export default {
  data: () => ({
    drawer: false,
    group: null,
    background: true,
    showMainTitle: true,
    numberOfMethod: ['Методика исключения', 'Методика интервалов', 'Методика высиживания', 'Методика фейдинга']
  }),
  components: {},
  watch: {
    group () {
      this.drawer = true
    }
  },
  methods: {
    ...mapActions([
      'SET_NUMBER_METHOD'
    ]),
    doLinks (data) {
      this.SET_NUMBER_METHOD(data)
      switch (data) {
        case 0:
          this.SET_NUMBER_METHOD(0)
          this.showMainTitle = false
          break
        case 1:
          this.SET_NUMBER_METHOD(1)
          this.showMainTitle = false
          break
        case 2:
          this.SET_NUMBER_METHOD(2)
          this.showMainTitle = false
          break
        case 3:
          this.SET_NUMBER_METHOD(3)
          this.showMainTitle = false
          break
        case 4:
          this.showMainTitle = true
          break
        default:
          this.SET_NUMBER_METHOD(0)
          this.showMainTitle = true
      }
      this.drawer = false
    }
  },
  computed: {
    ...mapGetters(['IS_METHODS', 'IS_NUMBER_METHOD']),
    links () {
      return [
        {
          id: 0,
          icon: 'mdi-chevron-down-circle',
          text: 'Методика исключения',
          link: '/first'
        },
        {
          id: 1,
          icon: 'mdi-chevron-down-circle',
          text: 'Методика интервалов',
          link: '/second'
        },
        {
          id: 2,
          icon: 'mdi-chevron-down-circle',
          text: 'Методика высиживания',
          link: '/third'
        },
        {
          id: 3,
          text: 'Методика фейдинга',
          icon: '',
          link: '/fourth'
        },
        {
          id: 4,
          icon: 'mdi-chevron-down-circle',
          text: 'Вопросы-ответы',
          link: '/faq'
        }
      ]
    }
  }
}
</script>
