<template>
  <div class="v-pop-collection">
    <div
      :aria-hidden="modalToggle"
      :class="{'vuexplosive-modal-hidden': !active, 'vuexplosive-modal-visible': active}"
      @keydown.esc="active = false"
      class="vuexplosive-modal"
      role="dialog"
      tabindex="-1"
    >
      <transition name="scale">
        <div class="vuexplosive-modal-container" v-if="active">
          <div class="vuexplosive-modal-inner ">
            <div style="width: 90%;margin:auto" class="">
              <img src="http://vomit.mrk.digital/img/close.png" alt="close" class="iconClose" @click="modalToggle">
          </div>
            <div class="vuexplosive-modal-header d-flex justify-space-around">
              <div>
                <v-flex class="title-new">
                Тест для ребенка {{IS_NUMBER_PAGE}} месяцев.
               </v-flex>
              </div>

            </div>

            <div class="vuexplosive-modal-content text-center">

              <div  class="justify-center window-quiz" :key="renderAddInn">
                <v-flex class="title-quiz">
                Ваш ребенок:
                </v-flex>
                <v-flex class="main-quiz">
                 {{question}}
                </v-flex>
                <v-card
                  class="mx-auto"
                  max-width="300"
                >
                  <v-list>
                    <v-list-item-group v-model="model"  color="#2c0d57" active-class="border" >
                      <v-list-item
                        v-for="(item, i) in items"
                        :key="i"
                      >
                       <v-list-item-content class="border-simple">
                          <v-list-item-title v-text="item.text"></v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </v-card>

                <div style="width: 80%; margin: 12px auto 0 auto" class="d-flex justify-space-between" >
                  <div class="btn-quiz" @click="pagePrev()" >
                    <img src="http://vomit.mrk.digital/img/arrow-left.png" alt="close" class="iconArrow"/>
                  </div>
                  <div>
<!--                    {{arrayCounter}}-->
                  </div>
                  <div class="btn-quiz" @click="pageNext()">
                    <img src="http://vomit.mrk.digital/img/arrow-right.png" alt="close" class="iconArrow"/>
                  </div>
                </div>
                <div class="rec-character" v-if="!showAttention">
                *ВНИМАНИЕ! Каждый ребенок индивидуален и окончательную оценку способен сделать только ваш доктор. Данный тест носит рекомендательный характер.
                </div>
                <div class="rec-character-red" v-if="showAttention">
                  ВНИМАНИЕ! Вы не выбрали ни один из вариантов!
                </div>
              </div>

            </div>

          </div>
        </div>
      </transition>

      <div @click="modalToggle"  class="vuexplosive-modal-bg">

      </div>
    </div>
  </div>
</template>

<script>
// import VPagination from '../v-pagination/__old'
import { mapActions, mapGetters } from 'vuex'
/* eslint-disable */
  export default {
    name: "popUp Test",
    components:{
      // VPagination
    },
    computed: {
      ...mapGetters(['IS_NUMBER_PAGE', 'IS_ALL_DATA_QUIZ']),
      question(){
       return (this.counter+1)  + ". " + this.IS_ALL_DATA_QUIZ[this.IS_NUMBER_PAGE][this.counter].text
      },
      setLink(){
        return this.inLink
        // return 'https://www.youtube.com/embed/nsHPtpOEdhQ?rel=0'
      }
    },
    props: {
      visible: {
        default: false
      },
      inTitle: {
        default: "Видео"
      },
      videoId:{
        default: null
      },
      inLink: {
        default: null
      }
    },
    data: function() {
      return {
        items: [
          {
              text: 'Нет'
          },
          {
              text: 'Да'
          }
        ],
        model: null,
        active: false,
        show: false,
        counter: 0,
        totalCounter:0,
        arrayCounter:[],
        renderAddInn: 0,
        showAttention: false,
        explosionGifUrl: '',
        explosionGifUrlBlob: '',
        windowWidth: null,
        windowHeight: null
      };
    },
    created() {
      fetch(this.explosionGifUrl)
        .then(response => response.blob())
        .then(images => {
          this.explosionGifUrlBlob = URL.createObjectURL(images);
        });
      window.addEventListener('resize', this.updateWidth)
      window.addEventListener('resize', this.updateHeight)
      this.updateWidth()
      this.updateHeight()
    },
    methods: {
      ...mapActions([
        'SET_RESULT_QUIZ'
      ]),
      pageNext(){
        let length = this.IS_ALL_DATA_QUIZ[this.IS_NUMBER_PAGE].length
        if(this.counter <= (length-1) ) {
          if(this.model !== null){
            this.counter++
            this.renderAddInn++
            this.arrayCounter.push(this.model)
            this.showAttention = false
            this.model = null
          } else {
            this.showAttention = true
          }
        } else {
          this.SET_RESULT_QUIZ(this.arrayCounter)
          this.$emit('startResult')
          this.modalToggle()
        }
      },
      pagePrev(){
        if(this.counter > 0){
          this.counter--
          this.renderAddInn++
          this.arrayCounter.pop()
        }else {

        }

      },
      updateWidth () {
        this.windowWidth = window.innerWidth
      },
      updateHeight () {
        this.windowHeight = window.innerHeight
      },
      modalToggle() {
        this.arrayCounter = []
        this.totalCounter =  this.counter = 0
        this.model = null
        this.active = !this.active
      }
    },
    watch: {
      visible(oldVal, newVal) {
        this.active = !this.active
      }
    }
  };
</script>


