<template>
  <v-container>
    <div v-if="startIntro" class="superCenter">
    <v-row align="center" class="justify-center ">
      <v-col
        cols="12"
        md="auto"
        class="intro"
      >
        <h3 class="text-center pb-4">
          Вас приветствует Web-приложение, готовое помочь в решении Ваших проблем со сном ребенка.
        </h3>
        <p>
          Внимательно ознакомьтесь с данной инструкцией.
          Выберите одну из предложенных 4 методик для КРАТКОГО ознакомления. Если методика Вас заинтересовала, нажмите
          кнопку “Дальше” и, перейдя к подробному видео,
          ознакомьтесь с методикой. Для работы по этой методике нажмите кнопку “ Этапы работы” и начните работать.
          Если вы понимаете, что эта методика Вам не подходит, вернитесь на шаг назад через кнопку ”Назад” и выберите
          другой вариант
        </p>
        <p>
          <b>Внимание!</b> Разделы по работе с дневными снами, ранними пробуждениями, пролонгацией дневных снов,
          ритуалами, ночными кормлениями
          и т.д. расположены в разделе "Этапы работы" в каждой из методик.
          Перед началом работы походите по вкладкам меню, для лучшей ориентации.
        </p>
      </v-col>

    </v-row>
    <v-row class="justify-center text-center">
      <v-col
        cols="12"
        md="auto"
      >
        <v-btn class="btn-common" @click="startIntro = !startIntro">
          <p>
            Старт
          </p>
        </v-btn>
      </v-col>
    </v-row>
    </div>
   <div  v-if="!startIntro" class="superCenter">
  <v-row class="justify-center text-center">
      <v-col
        cols="12"
        md="auto"
      >
        <v-btn class="btn-common" @click="goToMethod(0)">
          <p>
            Методика исключения
          </p>
        </v-btn>
      </v-col>
    </v-row>
    <v-row class="justify-center text-center">
      <v-col
        cols="12"
        md="auto"
      >
        <v-btn class="btn-common" @click="goToMethod(1)" >
          <p>
            Методика интервалов
          </p>
        </v-btn>
      </v-col>
    </v-row>
    <v-row class="justify-center text-center">
      <v-col
        cols="12"
        md="auto"
      >
        <v-btn class="btn-common" @click="goToMethod(2)">
          <p>
            Методика высиживания
          </p>
        </v-btn>
      </v-col>
    </v-row>
    <v-row class="justify-center text-center">
      <v-col
        cols="12"
        md="auto"
      >
        <v-btn class="btn-common" @click="goToMethod(3)">
          <p>
            Методика Фейдинга
          </p>
        </v-btn>
      </v-col>
    </v-row>
    </div>
  </v-container>
</template>

<script>

import { mapActions } from 'vuex'

export default {
  name: 'v-main-page',
  methods: {
    ...mapActions([
      'SET_NUMBER_METHOD'
    ]),
    goToMethod (data) {
      switch (data) {
        case 0:
          this.$router.push('/first')
          this.SET_NUMBER_METHOD(0)
          break
        case 1:
          this.$router.push('/second')
          this.SET_NUMBER_METHOD(1)
          break
        case 2:
          this.$router.push('/third')
          this.SET_NUMBER_METHOD(2)
          break
        case 3:
          this.$router.push('/fourth')
          this.SET_NUMBER_METHOD(3)
          break
        default:
          this.SET_NUMBER_METHOD(0)
          this.$router.push('/first')
      }
    }
  },
  data: () => ({
    startIntro: true
  })
}
</script>
