<script>
import { mapGetters } from 'vuex'

export default {
  created () {
    window.addEventListener('resize', this.updateWidth)
    window.addEventListener('resize', this.updateHeight)
    this.updateWidth()
    this.updateHeight()
  },
  name: 'v-main-page',
  computed: {
    ...mapGetters(['IS_METHODS']),
    heightVideo () {
      if (this.windowHeight < 600) {
        return 480
      } else if (this.windowHeight < 900) {
        return 500
      } else {
        return 660
      }
    }
  },
  methods: {
    backFirst () {
      this.startIntro = !this.startIntro
      this.stagesOfWork = false
    },
    stages () {
      this.startIntro = !this.startIntro
      this.stagesOfWork = !this.stagesOfWork
      this.render++
    },
    sentToVideo () {

    },
    startResult () {
      this.showResult = !this.showResult
    },
    updateWidth () {
      this.windowWidth = window.innerWidth
    },
    updateHeight () {
      this.windowHeight = window.innerHeight
    }
  },
  data: () => ({
    show: false,
    showTest: false,
    showResult: false,
    startIntro: true,
    stagesOfWork: false,
    title: null,
    link: null,
    videoId: 0,
    render: 1,
    newVideo: 'https://www.youtube.com/embed/DMhIQSOkr-Y?rel=0',
    windowWidth: null,
