<template>
  <v-container key="render" >
    <div v-if="!stagesOfWork" >
    <div v-if="startIntro" class="superCenter">
    <v-row align="center" class="justify-center">
      <v-col
        cols="10"
        md="auto"
        class="intro"
      >
        <h3 class="text-center pb-4">
          Методика фейдинга
        </h3>
        <p v-html="IS_METHODS[3].text" />
      </v-col>

    </v-row>
    <v-row class="justify-center text-center">
      <v-col
        cols="12"
        md="auto"
      >
        <v-btn class="btn-common" @click="startIntro = !startIntro">
          <p>
              Дальше
          </p>
        </v-btn>
      </v-col>
    </v-row>
    </div>
   <div  v-if="!startIntro" class="mainButtons">
     <v-row class="justify-center text-center mt-6">
       <v-col
         cols="12"
         md="auto"
       >
         <iframe
           :width="widthVideo"
           title="YouTube video player"
           :height="heightVideo"
           :src="IS_METHODS[3].video"
           frameborder="0"
           allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
           allowfullscreen
         ></iframe>
       </v-col>
     </v-row>
  <v-row class="justify-center text-center">
      <v-col
        cols="12"
        md="auto"
      >
        <v-btn class="btn-common" @click="stages()">
          <p>
             Этапы работы
          </p>
        </v-btn>
      </v-col>
    </v-row>
     <v-row class="justify-center text-center">
       <v-col
         cols="12"
         md="auto"
       >
         <v-btn class="btn-back" @click="backFirst">
           <p>
            Назад
           </p>
         </v-btn>
       </v-col>
     </v-row>
    </div>
    </div>
    <v-stages v-if="stagesOfWork" @myEvent="backFirst"/>
  </v-container>
</template>

<script>
import vStages from './v-stages'
import { mapGetters } from 'vuex'

export default {
  created () {
    window.addEventListener('resize', this.updateWidth)
    window.addEventListener('resize', this.updateHeight)
    this.updateWidth()
    this.updateHeight()
  },
  name: 'v-main-page',
  components: {
    vStages
  },
  computed: {
    ...mapGetters(['IS_METHODS']),
    widthVideo () {
      if (this.windowWidth > 600) {
        return this.heightVideoCalc() * 3 / 4
      } else {
        return this.heightVideoCalc() * 8 / 16
      }
    },
    heightVideo () {
      if (this.windowHeight < 600) {
        return 480
      } else if (this.windowHeight < 900) {
        return 500
      } else {
        return 660
      }
    }
  },
  methods: {
    heightVideoCalc () {
      if (this.windowHeight < 600) {
        return 480
      } else if (this.windowHeight < 900) {
        return 500
      } else {
        return 660
      }
    },
    backFirst () {
      this.startIntro = !this.startIntro
      this.stagesOfWork = false
    },
    stages () {
      this.startIntro = !this.startIntro
      this.stagesOfWork = !this.stagesOfWork
      this.render++
    },
    updateWidth () {
      this.windowWidth = window.innerWidth
    },
    updateHeight () {
      this.windowHeight = window.innerHeight
    }
  },
  data: () => ({
    startIntro: true,
    stagesOfWork: false,
    render: 1,
    newVideo: 'https://www.youtube.com/embed/DMhIQSOkr-Y?rel=0',
    windowWidth: null,
    windowHeight: null
  })
}
</script>
<style scoped lang="scss">
.main-container {
  padding-bottom: 24px;
}
</style>
