<template>
  <div class="v-pop-collection">
    <div
      :aria-hidden="modalToggle"
      :class="{'vuexplosive-modal-hidden': !active, 'vuexplosive-modal-visible': active}"
      @keydown.esc="active = false"
      class="vuexplosive-modal"
      role="dialog"
      tabindex="-1"
    >
      <transition name="scale">
        <div class="vuexplosive-modal-container" v-if="active">
          <div class="vuexplosive-modal-inner ">
            <div style="width: 90%;margin:auto" class="">
              <img src="http://vomit.mrk.digital/img/close.png" alt="close" class="iconClose" @click="modalToggle">
          </div>
            <div class="vuexplosive-modal-header d-flex justify-space-around">
              <div>
                <v-flex class="title-new">
                Результат теста для ребенка {{IS_NUMBER_PAGE}} месяцев.
               </v-flex>
              </div>

            </div>

            <div class="vuexplosive-modal-content text-center">

              <div  class="justify-center window-quiz" :key="renderAddInn">
                <img src="http://vomit.mrk.digital/img/baby_cool.png" height="200px" width="176px" alt="baby">
                <v-flex >
                 {{result}}
                  <br>
<!--                  {{IS_RESULT_QUIZ}}-->
                </v-flex>
                 <div class="rec-character" >
                ВНИМАНИЕ! Каждый ребенок индивидуален и окончательную оценку способен сделать только ваш доктор. Данный тест носит рекомендательный характер.
                </div>
              </div>

            </div>

          </div>
        </div>
      </transition>

      <div @click="modalToggle"  class="vuexplosive-modal-bg">

      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'popUp result',
  computed: {
    ...mapGetters(['IS_NUMBER_PAGE', 'IS_RESULT_QUIZ', 'IS_ANSWERS']),
    result () {
      let sum = 0
      for (let i = 0; i < this.IS_RESULT_QUIZ.length; i++) {
        sum += this.IS_RESULT_QUIZ[i]
      }
      sum = (sum / this.IS_RESULT_QUIZ.length) * 100
      if (sum < 20) {
        return this.IS_ANSWERS[2]
      } else if (sum < 50) {
        return this.IS_ANSWERS[1]
      } else {
        return this.IS_ANSWERS[0]
      }
    }
  },
  props: {
    visible: {
      default: false
    }
  },
  data: function () {
    return {
      active: false,
      show: false,
      counter: 0,
      totalCounter: 0,
      arrayCounter: [],
      explosionGifUrl: '',
      explosionGifUrlBlob: '',
      windowWidth: null,
      windowHeight: null
    }
  },
  created () {
    fetch(this.explosionGifUrl)
      .then(response => response.blob())
      .then(images => {
        this.explosionGifUrlBlob = URL.createObjectURL(images)
      })
    window.addEventListener('resize', this.updateWidth)
    window.addEventListener('resize', this.updateHeight)
    this.updateWidth()
    this.updateHeight()
  },
  methods: {
    modalToggle () {
      this.totalCounter = this.counter = 0
      this.model = null
      this.active = !this.active
    }
  },
  watch: {
    visible (oldVal, newVal) {
      this.active = !this.active
    }
  }
}
</script>

