import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../components/v-main-page'
import first from '../components/methodsOfDream/v-first-method'
import second from '../components/methodsOfDream/v-second-method'
import third from '../components/methodsOfDream/v-third-method'
import fourth from '../components/methodsOfDream/v-fourth-method'
import faq from '../components/v-faq'
import guide from '../components/v-quide'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: home
  },
  {
    path: '/first',
    name: 'first',
    component: first
  },
  {
    path: '/second',
    name: 'second',
    component: second
  },
  {
    path: '/third',
    name: 'third',
    component: third
  },
  {
    path: '/fourth',
    name: 'fourth',
    component: fourth
  },
  {
    path: '/faq',
    name: 'faq',
    component: faq
  },
  {
    path: '/guide',
    name: 'guide',
    component: guide
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
